<template>
  <form class="card" @submit.prevent="submit" style="overflow: hidden">
    <div class="card-header">
      <h5 class="card-title text-center">Quadro societário</h5>
    </div>
    <div class="card-body">
      <transition-group name="list" tag="div">
        <template v-for="(socio, index) in socios">
          <div :key="index + 1">
            <div class="d-flex justify-content-between align-items-center mb-3">
              <p class="m-0 font-weight-bold">Sócio {{index + 1}}</p>
              <button class="btn btn-sm btn-danger" type="button" @click="socios.splice(index, 1)">REMOVER SÓCIO</button>
            </div>
            <div class="form-group">
              <label :for="'ipt-socio-' + index">Nome</label>
              <input
                :id="'ipt-socio-' + index"
                type="text"
                class="form-control"
                v-model="socios[index].nome"
                placeholder="Nome completo"
                :class="{'is-invalid': socios[index].nome.length === 0}"
                required
              >
            </div>
            <div class="form-group">
              <label :for="'ipt-cpf-' + index">CPF</label>
              <the-mask
                :id="'ipt-cpf-' + index"
                mask="###.###.###-##"
                type="tel"
                class="form-control"
                v-model="socios[index].cpf"
                placeholder="Digite o CPF do sócio"
                :class="{'is-invalid': socios[index].cpf.length !== 11}"
                required
              ></the-mask>
            </div>
            <div class="form-group">
              <label :for="'ipt-cota-' + index">Cota</label>
              <div class="input-group">
                <div class="input-group-prepend">
                  <span class="input-group-text">%</span>
                </div>
                <money
                  :id="'ipt-cota-' + index"
                  class="form-control"
                  v-model="socios[index].cota"
                  v-bind="mask"
                  :class="{'is-invalid': socios[index].cota > 100 || socios[index].cota === 0}"
                ></money>
                <div class="invalid-feedback" v-if="socios[index].cota > 100">A cota não pode ser maior que 100%</div>
                <div class="invalid-feedback" v-if="socios[index].cota === 0">A cota não pode ser ZERO</div>
              </div>
            </div>
            <hr class="my-4">
          </div>
        </template>
        <div v-if="socios.length === 0" :key="999">
          <p class="text-center text-danger mb-4 mt-3">Nenhum sócio cadastrado</p>
          <hr>
        </div>
      </transition-group>
      <p class="mb-0 text-center" :class="{'text-success': cotaTotal === 100}">Soma das cotas: {{cotaTotal}} %</p>
      <div class="invalid-feedback d-block text-center" v-if="cotaTotal !== 100">A soma das cotas tem que ser 100 %</div>
      <button class="btn btn-outline-primary btn-block mt-3" type="button" @click="socios.push({nome: '', cpf: '', cota: 0})">
        <i class="fas fa-plus pr-2"></i>ADICIONAR SÓCIO
      </button>
    </div>
    <div class="card-footer text-center">
      <button type="button" class="btn btn-primary" @click="$router.push({ name: 'recadpj-3', params: {empresa: empresa} })">VOLTAR</button>
      <button type="submit" class="btn btn-success">AVANÇAR</button>
    </div>
  </form>
</template>

<script>
  export default {
    name: "PjSocios",
    props: {
      empresa: {required: true, type: Object},
      value: {default: () => ({}), type: Object}
    },
    data: () => ({
      socios: [],
      mask: {
        decimal: '.',
        precision: 2,
        masked: false,
      }
    }),
    computed: {
      cotaTotal() {
        if (!this.socios || this.socios.length === 0) return 0;
        const cota = this.socios.reduce((carry, item) => carry + item.cota, 0);
        return (cota > 99.991 && cota <= 100) ? 100 : cota.toFixed(2);
      }
    },
    methods: {
      submit() {
        //Possui pelo menos um sócio na lista?
        if (this.socios.length === 0) {
          this.$toastr.error('Você precisa cadastrar os sócios');
          return;
        }
        //A cota total é 100%?
        if (this.cotaTotal !== 100) {
          this.$toastr.error('A cota total não está 100%');
          return;
        }
        //Há sócio com cota ZERO? Não pode!
        if (this.socios.find(s => s.cota === 0)) {
          this.$toastr.error('Não pode haver sócio com cota ZERO');
          return;
        }
        //Todos os CPF's são válidos?
        if (this.socios.find(s => s.cpf.length !== 11)) {
          this.$toastr.error('Os CPFs precisam ser válidos');
          return;
        }

        const dados = this.value;
        dados.socios = this.socios;
        this.$emit('input', dados);
        this.$emit('recadastrar');
      },
      //Rotina interna
      popularCampos(e) {
        this.socios = e.socios.sort((a,b) => {
          if (a.cota > b.cota) return -1;
          else if (a.cota < b.cota) return 1;
          else return 0;
        });
      }
    },
    mounted() {
      this.popularCampos(this.empresa);
    },
    beforeRouteLeave(to, from, next) {
      to.params.empresa = this.empresa;
      next();
    }
  }
</script>

<style>
  .list-enter-active, .list-leave-active {
    transition: all 500ms;
  }
  .list-enter, .list-leave-to {
    opacity: 0;
    transform: translateX(30px);
  }
</style>